.tile-accordion-item {
	@apply md:rounded-px10 md:bg-white md:shadow-md border-teal-dark;

	@screen md {
		.tile-cta {
			@include stretched-link;
		}
		&:hover {
			@apply bg-gray-light transition-colors;
			.tile-cta {
				@apply border-none pb-2;
				background: transparent url("/wp-content/themes/trevor/static/media/h-divider-wave.svg") repeat-x 1px 100%;
				background-size: 14.5px 6px;
			}
		}
	}
}


.accordion-button {
	&:after {
		@extend .trevor-ti-chevron-down:before;
		@apply mt-2 text-px14 font-bold;
	}

	&:not(.collapsed) {

		&::after {
			@apply mt-0;
			transform: rotate(180deg);
		}
	}

	@screen md {
		&:after {
			@apply hidden;
		}
	}
}

.collapse {
	.accordion-body {
		@apply border-b border-blue_green border-opacity-20;

		@screen md {
			@apply border-0;
		}
	}

	&:not(.show) {
		@apply hidden;

		@screen md {
			@apply flex;
		}
	}
}
